// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import { Toast, MessageBox } from 'mint-ui';
import 'mint-ui/lib/style.css'
import {
    formatDate,
    minutesFormat,
    money
} from '../src/assets/js/date';
Vue.config.productionTip = false
Vue.prototype.$toast = Toast;
Vue.prototype.$confirm = MessageBox;
import '../src/assets/js/axios.package';
import '../src/assets/js/rem';
import '../src/assets/scss/reset.scss';
/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    components: { App },
    template: '<App/>'
})

Vue.filter('formatDate', (value, fmt) => {
    return formatDate(value, fmt)
})
Vue.filter('minutesFormat', (value, fmt) => {
    return minutesFormat(value, fmt)
})
Vue.filter('money', (str) => {
    return money(str)
})
let str = location.href; //取得整个地址栏
if(str.indexOf("?from=singlemessage")>-1){
  window.location.href=str.replace(str.substring(str.indexOf("?"),str.indexOf("#")),"")
}
