<template>
  <div>
    <div class="notice_box">
      <div class="article_content">
        <p v-html="data.content"> {{data.content}}</p>
      </div>
    </div>
  </div>

</template>

<script>
  const u = navigator.userAgent;
  var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
  var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
  import  comment from '../subViews/comment'
  import api from '@/api/share'
  export default {
    name: 'articleDetails',
    components:{comment,api},
    data () {
      return {
        data:{},
      }
    },
    mounted(){
      this.GetarticleDetail()
    },
    methods: {
      Getimg(event){
        if (event.target.tagName=='IMG'){
          if(isAndroid){
            android.showImg(event.target.currentSrc);}
          else  if(isiOS){
            window.location.href='image-preview:'+event.target.currentSrc;
          }
        }
      },
      GetarticleDetail() {
        api.advertisementsdetails({
        }).then(res => {
          if (res.status == 1) {
            this.data=res.data
            document.getElementsByClassName("article_content")[0].addEventListener('click',this.Getimg)
          } else
            this.$toast(res.msg)
        })
      },
    },
    destroyed:function () {
      document.getElementsByClassName("article_content")[0].removeEventListener('click',this.Getimg)
    }
  }
</script>
<style scoped>
  .article_content{
    font-size: .15rem;
    color: #666666;
    letter-spacing: 0;
    line-height: .3rem;
  }
   .article_content img{
    width: 100%;
    object-fit: cover;
  }
  .article_content video{
    width: 100%;
    height: 75%;
    margin: 10px 0;
  }
</style>



